  @import url("https://use.typekit.net/sdk5zav.css");

/* SCSS HEX */
@intense-cherry: #BB333Bff;
@electric-rose: #D848A9ff;
@frosted-blue: #A8EAF6ff;
@midnight-violet: #361722ff;

body {
  margin: 0;
  padding: 0;
  overflow-y: auto;
  overflow-y: scroll;
  overflow-x: hidden;
  box-sizing: content-box;
  background-color: rgb(2, 12, 22);
  /* font-family: "Electrolize", sans-serif; */
   font-family: "proxima-nova", sans-serif;
}

.app {
  width: 100dvw;
  /* height: 100dvh; */
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100dvw;
  z-index: 90;
  padding: 1vh;
  backdrop-filter: blur(30px);
  /* background-color: rgba(0, 0, 0, 0.7); */
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.49),
    rgba(0, 0, 0, 0)
  );
}

ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
}

.nav-link {
  text-align: center;
  color: aliceblue;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  position: relative;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.nav-link:hover {
  transform: scale(1.1);
    /* background-size: 500% auto; */

}

.nav-link::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background: linear-gradient(
    to right,
    #da4d6e 30%,
    #25f4fb 80%
  );
  transform-origin: bottom center;
  transition: transform 0.33s ease-in-out;
}

.nav-link:hover::after {
  transform: scaleX(1);
  transform-origin: bottom center;
}

.three-container {
  position: relative;
  z-index: 0;
  width: 100%;
  height: 100vh;
  display: block;
  margin: 0;
  padding: 0;
}

.three-canvas {
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);
}

@keyframes textShine {
    0% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}

.home-text {
  font-family: acktiv;font-family: "aktiv-grotesk", sans-serif;
  font-weight: 800;
  font-style: normal;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: rgb(211, 234, 255);
  text-shadow: #0a141f90 1px 1px 5px;
  z-index: 1;
  text-transform: uppercase;
  pointer-events: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home-title {
  font-size: 4rem;
  margin: 0;
  padding: 0;
  letter-spacing: 1rem;
  font-weight: 800;
}


.home-subtitle {
  font-size: 1rem;
  margin: 0;
  padding: 0;
  letter-spacing: 0.5rem;
  font-weight: 200;
}


@media only screen and (max-width: 600px) {
  .home-text {
    width: 80%;
  }

  .home-title {
    font-size:2rem;
    letter-spacing: 1rem;
  }

  .home-subtitle {
    width: 80%;
    font-size: 0.5rem;
    letter-spacing: 0.2rem;
  }
}
